.Loader_pageLoader__28_NM {
    width: 150px;
    height: 150px;
    position: relative;
}

.Loader_pageLoader__28_NM circle {
    fill: none;
    stroke-width: 5;
    stroke-linecap: round;
    -webkit-animation-name: Loader_loader__WnvGp;
            animation-name: Loader_loader__WnvGp;
    -webkit-animation-duration: 4s;
            animation-duration: 4s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-timing-function: ease-in-out;
            animation-timing-function: ease-in-out;
    -webkit-transform-origin: center center;
            transform-origin: center center;
}

.Loader_pageLoader__28_NM circle:nth-child(1) {
    stroke: #ffc114;
    stroke-dasharray: 50;
    -webkit-animation-delay: -0.2s;
            animation-delay: -0.2s;
}

.Loader_pageLoader__28_NM circle:nth-child(2) {
    stroke: #ff5248;
    stroke-dasharray: 100;
    -webkit-animation-delay: -0.4s;
            animation-delay: -0.4s;
}

.Loader_pageLoader__28_NM circle:nth-child(3) {
    stroke: #19cdca;
    stroke-dasharray: 180;
    -webkit-animation-delay: -0.6s;
            animation-delay: -0.6s;
}

.Loader_pageLoader__28_NM circle:nth-child(4) {
    stroke: #4e88e1;
    stroke-dasharray: 350;
    stroke-dashoffset: -100;
    -webkit-animation-delay: -0.8s;
            animation-delay: -0.8s;
}

@-webkit-keyframes Loader_loader__WnvGp {
    50% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes Loader_loader__WnvGp {
    50% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}
.Loading_loading__1DHfV {
    width: 150px;
    height: 150px;
    position: absolute;
    top: calc(50% - 75px);
    left: calc(50% - 75px);
}
